footer.pass-footer {
  position: absolute;
  right: 50%;
  bottom: 0;
  display: block;
  text-align: center;
  transform: translateX(50%) translateY(-50%);
}

.tesla {
  --tesla-segments: 5;
  --tesla-angle: 45;
  --tesla-blur: 5;
  --tesla-width: 4;
  --tesla-hue: 180;
  --tesla-saturation: 100;
  --tesla-lightness: 50;
  background: paint(tesla-coil);
  padding: 20px 30px;
  border: 5px solid darkslategrey;
  font-weight: 500;
}
